import glanceSlideNavTabBar from '@/components/glance-SlideNavTabBar.vue';
let id = ""
export default {
	components: {
		glanceSlideNavTabBar
	},
	data() {
		return {
			isShowRecommend: false,
			recommendList: [1, 2, 3, 4, 5, 6],
			tabIndex: 0,
			info: {}
		};
	},
	computed: {
		recommendBoxWidth() {
			return uni.upx2px(this.recommendList.length * 270) + 'px';
		}
	},
	onLoad(options) {
		id = options.id
		this.init()
	},
	onPullDownRefresh() {
		this.init();
	},
	methods: {
		init() {
			this.api.classify.get_sub_category_header({
				categoryId: id
			}, res => {
				console.log(res)
				this.info = res.data
				uni.setNavigationBarTitle({
					title: res.data.categoryName
				});
			})
		},
		toggleFollowed(el) {
			this.api.classify.toggle_followed({
				vid: el.categoryId,
				action: el.isFollowed ? 0 : 1
			}, res => {
				console.log(res)
				this.info.isFollowed = !this.info.isFollowed
			})
		},
		showRecommend() {
			this.isShowRecommend = !this.isShowRecommend;
		},
		clickitem(index, val) {
			this.tabIndex = index
		},
		changeSwiper(e) {
			this.tabIndex = e.target.current
		}
	}
};
